import React, {useEffect, useState} from 'react';
import {connect} from "react-redux";
//import { gsap,Power4 } from "gsap";
import store from 'src/store';

const ControlBlock = (props) => {
    const [innerContent,setInnerContent] = useState([]);
    //const [mustRoll, setMustRoll] = useState(true);
    //const thisclass=props.blockClass ? props.blockClass : "";
    const wid = props.width ? props.width : 100;

    useEffect(()=>{
            //gsap.set(".wb-ctrl-btn", {scale:0});
            //gsap.set("."+thisclass, {scale:0});
            //gsap.set(".controls", {x: 300});

            //gsap.to(".wb-ctrl-btn", .2, {scale:1,stagger:.1});
            //gsap.to("."+thisclass, .2, {scale:1,stagger:.1});
            //gsap.to(".controls", 2, {x:10,ease:Power4.easeOut});
    },[]);

    let contentStore = store.getState().dataStore.mediaControls;

    useEffect(()=>{
        if(props.controlKey && store.getState().dataStore?.mediaControls?.hasOwnProperty(props.controlKey) ){
            setInnerContent(store.getState().dataStore.mediaControls[props.controlKey]);
        }
    },[contentStore, props.controlKey])

    useEffect(()=>{
        if(props.controls){
            setInnerContent(props.controls);
        }
    },[props.controls])

    const controlStyle={
        display:"flex",
        flexDirection:"row",
        flexJustify: "flex-end"
    };
    //                {renderControls(innerContent,thisclass)}
    return (
        <div style={{display:"flex", flexDirection:"rows", height:props.height, width:wid + "%"}}>
            <div className="controls" style={controlStyle}>
                {innerContent}
            </div>
        </div>
    )
};

const mapStateToProps = (state) =>{
    return {
        dataStore:state.dataStore
    };
};

export default connect(mapStateToProps,{})(ControlBlock);
